.content {
    width: 1000px;
    margin: 0 auto;
    background-color: #fff;
}

.img_wrap {
    width: 400px;
    height: 500px;
}

.img_big img {
    width: 100%;
    height: 100%;
}

.img_big {
    width: 100%;
    height: 400px;
    padding: 10px 20px;
}

.img_small {
    width: 100%;
    font-size: 0;
}

.img_small li {
    float: left;
    width: 60px;
    height: 60px;
    margin-right: 10px;
    border: 2px solid transparent;
    /* padding: 5px; */
}

.img_small img {
    width: 50px;
    height: 100%;
    margin: 0 5px;
}

.img_small .active {
    border: 2px solid #ff4400;
}